<template>
  <div v-if="post">
    <h3>{{ post.title }}</h3>
    <p>
      <span>作者{{ post.author.loginname }}</span
      ><span>创建时间{{ post.create_at | moment("dddd, MMMM Do YYYY") }}</span>
    </p>
    <div v-html="post.content"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      post: null,
    };
  },
  async created() {
    const res = await this.$http.get(`/topic/${this.$route.params.id}`);
    this.post = res.data;
  },
};
</script>

